import { Grid } from "./Grid";
import { GridPoint } from "./AStarConst";
import { DebugUtils } from "@base/utils/DebugUtils";

/** é»˜è®¤æ‹ç‚¹æ•° */
const DEFAULT_TURN_COUNT = 2;
/** æ–¹å‘ */
const DIRECTION: number[][] = [[0, 1], [1, 0], [0, -1], [-1, 0]];
const DIRECTION_NAME = ["right", "down", "left", "up"];

/**
 * è¡¨ç¤ºè·¯å¾„æœç´¢ä¸­çš„èŠ‚ç‚¹ä¿¡æ¯
 */
class PathNode {
  public position: GridPoint; // å½“å‰èŠ‚ç‚¹çš„ä½ç½® [x, y]
  public g: number;           // ä»èµ·ç‚¹åˆ°å½“å‰èŠ‚ç‚¹çš„å®é™…ä»£ä»·
  public h: number;           // å½“å‰èŠ‚ç‚¹åˆ°ç›®æ ‡èŠ‚ç‚¹çš„å¯å‘å¼ä¼°è®¡ä»£ä»·
  public parent: PathNode | null; // å½“å‰èŠ‚ç‚¹çš„çˆ¶èŠ‚ç‚¹ï¼Œç”¨äºå›æº¯è·¯å¾„
  public direction: GridPoint | null; // ä»çˆ¶èŠ‚ç‚¹åˆ°å½“å‰èŠ‚ç‚¹çš„ç§»åŠ¨æ–¹å‘

  constructor(position: GridPoint, g: number, h: number, parent: PathNode | null = null, direction: GridPoint | null = null) {
    this.position = position;
    this.g = g;
    this.h = h;
    this.parent = parent;
    this.direction = direction;
  }

  /**
   * è·å–èŠ‚ç‚¹çš„æ€»ä»£ä»· f = g + h
   */
  public get f(): number {
    return this.g + this.h;
  }

  /**è·¯å¾„ä¿¡æ¯*/
  private getPathStr(): string {
    const list: string[] = [this.position.join("_")];
    let p = this.parent;
    while (p) {
      list.push(p.position.join("_"));
      p = p.parent;
    }
    return list.reverse().join(",");
  }

  /**è·¯å¾„ä¿¡æ¯*/
  public get pathStr(): string {
    return this.getPathStr();
  }

  /**æ–¹å‘*/
  public get directionName(): string {
    if (!this.direction) {
      return "";
    }
    for (let i = 0; i < DIRECTION.length; i++) {
      if (DIRECTION[i].toString() === this.direction.toString()) {
        return DIRECTION_NAME[i];
      }
    }
    return "";
  }

  /**
   * å…¨éƒ¨æ‹ç‚¹æ•°
   */
  public getTurnCountTotal(): number {
    if (!this.parent || !this.parent.direction || !this.direction) {
      return 0; // èµ·ç‚¹æ²¡æœ‰æ‹ç‚¹
    }
    let cnt = 0;
    let p = this.parent;
    let d = this.direction;
    while (p && d) {
      if (p.direction && d.toString() !== p.direction.toString()) {
        cnt++;
      }
      d = p.direction;
      p = p.parent;
    }
    return cnt;
  }
}

/**
 * A* ç®—æ³•ç±»
 */
export class AStar {
  private _grid: Grid; // ç½‘æ ¼å®ä¾‹
  private _turnCount: number = 0;// æ‹ç‚¹æ•°

  /**
   * æ„é€ å‡½æ•°
   * @param grid - Grid å®ä¾‹
   * @param turnCnt - æ‹ç‚¹æ•°ï¼Œé»˜è®¤2
   */
  constructor(grid: Grid, turnCnt = DEFAULT_TURN_COUNT) {
    this._grid = grid;
    this._turnCount = turnCnt;
  }

  /**
   * å¯å‘å¼å‡½æ•°ï¼Œç”¨äºä¼°è®¡ä»æŸä¸ªç‚¹åˆ°ç›®æ ‡ç‚¹çš„ä»£ä»·
   * æ­¤å¤„ä½¿ç”¨æ›¼å“ˆé¡¿è·ç¦»
   * @param a - å½“å‰ç‚¹ [x, y]
   * @param b - ç›®æ ‡ç‚¹ [x, y]
   * @returns æ›¼å“ˆé¡¿è·ç¦»
   */
  private heuristic(a: GridPoint, b: GridPoint): number {
    return Math.abs(a[0] - b[0]) + Math.abs(a[1] - b[1]);
  }

  /**
   * è·å–æŸä¸ªèŠ‚ç‚¹çš„æ‰€æœ‰é‚»å±…èŠ‚ç‚¹
   * @param node - å½“å‰èŠ‚ç‚¹
   * @param end - ç›®æ ‡ç‚¹
   * @returns é‚»å±…èŠ‚ç‚¹çš„ä½ç½®æ•°ç»„å’Œæ–¹å‘
   */
  private getNeighbors(node: PathNode, end: GridPoint): [GridPoint, GridPoint][] {
    const [x, y] = node.position;
    const list = DIRECTION.map(([dx, dy]) => ([[x + dx, y + dy], [dx, dy]] as [GridPoint, GridPoint]));
    return list.filter(([pos]) => end[0] === pos[0] && end[1] === pos[1]
      ? this._grid.isInBounds(pos[0], pos[1])
      : this._grid.isValid(pos[0], pos[1]));
  }

  /**
   * æ‰§è¡Œ A* ç®—æ³•ï¼ŒæŸ¥æ‰¾ä»èµ·ç‚¹åˆ°ç»ˆç‚¹çš„æœ€çŸ­è·¯å¾„
   * @param start - èµ·ç‚¹ [x, y]
   * @param end - ç»ˆç‚¹ [x, y]
   * @returns æœ€çŸ­è·¯å¾„çš„æ•°ç»„ï¼Œå¦‚æœæ— è·¯å¾„åˆ™è¿”å›ç©ºæ•°ç»„
   */
  public findPath(start: GridPoint, end: GridPoint): GridPoint[] {
    const openList: PathNode[] = [];        // æ‰“å¼€åˆ—è¡¨ï¼Œå­˜å‚¨å¾…å¤„ç†çš„èŠ‚ç‚¹
    const closedSet: Set<string> = new Set(); // å…³é—­åˆ—è¡¨ï¼Œå­˜å‚¨å·²å¤„ç†çš„èŠ‚ç‚¹
    const excludeSet: Set<string> = new Set(); // æ’é™¤çš„è·¯å¾„

    // åˆ›å»ºèµ·ç‚¹èŠ‚ç‚¹å¹¶åŠ å…¥æ‰“å¼€åˆ—è¡¨
    const startNode = new PathNode(start, 0, this.heuristic(start, end));
    openList.push(startNode);

    while (openList.length > 0) {
      // æŒ‰ç…§ f å€¼ + æ‹ç‚¹æ•°æ’åºï¼Œé€‰æ‹©æœ€ä¼˜çš„èŠ‚ç‚¹
      openList.sort((a, b) => (a.f + a.getTurnCountTotal()) - (b.f + b.getTurnCountTotal()));
      const currentNode = openList.shift()!; // å½“å‰èŠ‚ç‚¹
      if (excludeSet.has(currentNode.pathStr)) {
        continue;
      }
      DebugUtils.debugLog(currentNode.pathStr);

      // å¦‚æœåˆ°è¾¾ç»ˆç‚¹ï¼Œåˆ™å›æº¯è·¯å¾„
      if (currentNode.position[0] === end[0] && currentNode.position[1] === end[1]) {
        const path: GridPoint[] = [];
        let node: PathNode | null = currentNode;
        while (node) {
          path.unshift(node.position); // å°†è·¯å¾„åå‘æ’å…¥
          node = node.parent;
        }
        return path;
      }

      // å°†å½“å‰èŠ‚ç‚¹æ ‡è®°ä¸ºå·²å¤„ç†
      closedSet.add(currentNode.pathStr);

      // éå†å½“å‰èŠ‚ç‚¹çš„é‚»å±…
      const neighborList = this.getNeighbors(currentNode, end);
      for (const [neighbor, direction] of neighborList) {
        const neighborPath = currentNode.pathStr + "," + neighbor.toString();
        if (closedSet.has(neighborPath) || excludeSet.has(neighborPath)) {
          continue; // å¦‚æœé‚»å±…å·²å¤„ç†ï¼Œè·³è¿‡
        }

        const g = currentNode.g + 1; // ä»èµ·ç‚¹åˆ°é‚»å±…çš„å®é™…ä»£ä»·
        const h = this.heuristic(neighbor, end); // é‚»å±…åˆ°ç›®æ ‡çš„å¯å‘å¼ä»£ä»·
        const neighborNode = new PathNode(neighbor, g, h, currentNode, direction);
        if (neighborNode.getTurnCountTotal() > this._turnCount) {
          excludeSet.add(neighborPath);//æ’é™¤çš„è·¯å¾„
          continue;
        }
        const existingNode = openList.find(node => node.pathStr === neighborPath);

        // å¦‚æœé‚»å±…èŠ‚ç‚¹ä¸åœ¨æ‰“å¼€åˆ—è¡¨æˆ–æ–°çš„è·¯å¾„ä»£ä»·æ›´ä½
        if (!existingNode || g < existingNode.g) {
          if (existingNode) {
            // å¦‚æœé‚»å±…èŠ‚ç‚¹å·²å­˜åœ¨ï¼Œç§»é™¤æ—§çš„èŠ‚ç‚¹
            openList.splice(openList.indexOf(existingNode), 1);
          }
          // å°†é‚»å±…èŠ‚ç‚¹åŠ å…¥æ‰“å¼€åˆ—è¡¨
          openList.push(neighborNode);
        }
      }
    }

    // å¦‚æœæ‰“å¼€åˆ—è¡¨ä¸ºç©ºä¸”æœªæ‰¾åˆ°è·¯å¾„ï¼Œåˆ™è¿”å›ç©ºæ•°ç»„
    return [];
  }
}
